.navitem{
    margin:0.25em;
    font-size:1.25em;
    white-space:nowrap;
    text-transform:capitalize;
    text-decoration:none;
    color:var(--textColor);
    transition:inherit;
}
.icon{
    width:1em;
    margin-left:0.25em;
    margin-bottom:0.2em;
    vertical-align:middle;
}
.popupmsg{
    font-family:var(--mainFont);
    font-size:0.2em;
    max-width:40em;
    padding:2em;
    border-radius:1em;
    background-color:rgba(0,0,0,0.1);
    transform:translateY(2.25rem);
    box-shadow:0 0 10em rgba(0,0,0,0.05);
    text-align:center;
}
