.libraryscreen{
    max-width:100%;
    height:100%;
    overflow:hidden;
}
.sidebar *[class^=container]>*[class^=entryview] *[class^=entryview]:not([style]) *[class^=name]{
    text-transform:none !important;
}
.appicon{
    position:fixed;
    right:1em;bottom:1em;
}

@media(any-hover){
    .appicon:hover{
        transform:scale(1.1);
        filter:invert();
    }
}
