.preview{
    max-width:100%;
    display:flex;
    justify-content:space-around;
    align-items:center;
}
.draggableview{
    width:15em;
    height:15em;
    display:flex;
    justify-content:center;
    align-items:center;
    background-color:var(--mainColor);
    border-radius:1em;
    z-index:5;
}
.draggableview>h3{
    font-size:2em;
    text-align:center;
    text-transform:capitalize;
    color:var(--textColor);
}
.draggableview:first-of-type>h3{
    color:var(--backgroundColor);
}

@media(any-hover){
    .draggableview:hover{
        cursor:pointer;
        filter:brightness(0.9);
    }
}
