.preview{
    max-width:100%;
    display:flex;
    flex-direction:column;
    justify-content:flex-start;
    align-items:stretch;
}
.target{
    max-width:100%;
    padding:5em;
    margin-bottom:3em;
    border:0.5em solid var(--majorColor);
}
.navigator{
    width:100%;
    display:flex;
    justify-content:space-around;
    align-items:center;
    align-self:center;
    padding:3em;
    border-radius:2em;
    background-color:var(--minorColor);
}
.route{
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
}
.route>p{
    font-size:5em;
    text-align:center;
    color:var(--majorColor);
}

@media(min-width:15cm){
    .navigator{
        width:fit-content;
        justify-content:center;
    }
    .navigator>*{
        margin-left:15em;
    }
    .navigator>*:first-child{
        margin-left:0;
    }
}
