.appicon{
    width:fit-content;
    display:flex;
    flex-direction:column;
    justify-content:flex-start;
    align-items:center;
    row-gap:0.2em;
    text-decoration:none;
}
.appicon>.icon{
    width:2em;
    transform:rotateZ(deg);
}
.name{
    font-family:var(--minorFont);
    font-size:1em;
    text-transform:uppercase;
    letter-spacing:0.1em;
    color:var(--mainColor);
}
