.fieldheader{
    position:relative;
    width:100%;
    height:100%;
    display:flex;
    justify-content:flex-start;
    align-items:center;
    column-gap:1.5em;
}
.icon{
    position:absolute;
    width:5em;
    right:var(--spacing);
    top:0;bottom:0;margin:auto 0;
    transition:200ms;
}
.name{
    font-size:4.5em;
    color:var(--textColor);
}
.badge{
    font-size:2em;
    color:var(--minorColor);
    background-color:transparent;
    border:0.1em solid var(--minorColor);
}


@media(any-hover){
    .fieldheader[expandable]{
        transition:200ms;
    }
    .fieldheader[expandable]:hover{
        background:linear-gradient(to left,rgba(0,0,0,0.15) 10%,transparent);
    }
}
