.preview{
    position:relative;
    max-width:100%;
    height:50em;
    display:flex;
    flex-direction:column;
    justify-content:space-evenly;
    align-items:flex-start;
    align-content:space-between;
    flex-wrap:wrap;
    border:0.4em solid var(--mainColor);
    overflow:hidden;
    --spacing:1em;
}
.item{
    font-size:4em;
    text-transform:capitalize;
    color:white;
    border-radius:0.2em;
    padding:0.2em 0.4em;
    margin:calc(var(--spacing) / 4);
    background-color:var(--mainColor);
}
.popupview{
    width:calc(25em - 2 * var(--padding));
    height:calc(30em - 2 * var(--padding));
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    padding:var(--padding);
    border-radius:1em;
    background-color:var(--majorColor);
    --padding:2em;
}
.popupview>p{
    font-size:2.5em;
    text-align:center;
    color:var(--backgroundColor);
}

@media(max-width:15cm){
    .preview{
        height:40em;
        flex-direction:row;
        justify-content:space-evenly;
        align-content:space-evenly;
    }
    .item{
        margin:0;
    }
}
