.sidebarnavigator{
    max-width:100%;
}
.sidebar{
    background-color:transparent !important;
    color:var(--textColor);
    box-shadow:0 0 10em rgba(0,0,0,0.15);
}
.container{
    height:calc(100% - 2 * var(--paddingVertical)) !important;
    padding:var(--paddingVertical) 5% !important;
    --paddingVertical:5em;
}
.container::-webkit-scrollbar{
    width:1.5em;
    background-color:transparent !important;
}
.container::-webkit-scrollbar-thumb{
    border-radius:0.5em;
    background-color:rgba(0,0,0,0.25);
}
.container::-webkit-scrollbar-track{
    background-color:transparent !important;
}


@media(max-width:15cm){
    .sidebar{
        border-right:0.35em solid var(--minorColor);
        background:linear-gradient(to bottom left,var(--minorColor) -1000%,var(--backgroundColor)) !important;
    }
}
