.producemarket{
    width:fit-content;
    display:grid;
    grid-template-columns:repeat(2,1fr);
    gap:5em;
}
.name{
    font-size:8em;
    text-align:center;
    text-transform:capitalize;
    color:var(--mainColor);
    grid-column:span 2;
    justify-items:center;
}

@media(max-width:15cm){
    .producemarket>*{
        grid-column:span 2;
    }
}
