.headerview{
    position:relative;
    max-width:100%;
    display:flex;
    justify-content:space-between;
    align-items:center;
    padding:1em;
}
.navbar{
    flex:1;
    display:flex;
    justify-content:flex-start;
    align-items:center;
    flex-wrap:wrap;
    column-gap:3em;
    margin-left:2em;
    transition:500ms;
}

@media(max-width:15cm){
    .navbar{
        flex:1;
        flex-direction:row;
        justify-content:flex-start;
        margin-left:1em;
        column-gap:0.5em;
    }
}