.sidebarnavigator{
    position:relative;
    max-width:100%;
    height:100%;
    display:flex;
    justify-content:flex-start;
    align-items:flex-start;
    overflow:hidden;
    --transition:200ms;
}
.container{
    position:relative;
    flex:1;
    height:100%;
    z-index:0;
    overflow:auto;
}
