.footerview{
    position:relative;
    max-width:100%;
    display:flex;
    justify-content:space-between;
    align-items:center;
    padding:1em;
}
.col0{
    flex:1;
    display:flex;
    justify-content:flex-start;
    align-items:center;
}
.col1{
    display:flex;
    flex-direction:column;
    justify-content:flex-end;
    align-items:center;
    column-gap:0.35em;
}
.icon{
    width:2em;
}
.links{
    list-style:none;
    margin-left:1em;
}
.links>li{
    display:flex;
    justify-content:flex-start;
    align-items:center;
    margin-top:0.25em;
}
.links>li:first-of-type{
    margin-top:0;
}
.linkicon{
    width:1em;
    margin-right:0.25em;
}
.appicon{
    width:3em;
    /* margin-bottom:0.25em; */
}
.authoricon{
    width:3.5em;
    height:3.5em;
    background-position:center;
    background-repeat:no-repeat;
    background-size:cover;
    border-radius:50%;
    filter:opacity(0.25) brightness(0.9);
    overflow:hidden;
    transition:500ms;
}


@media(any-hover){
    .authoricon:hover{
        cursor:pointer;
        filter:opacity(1) brightness(1);
    }
}
@media(hover:none){
    .authoricon:active{
        cursor:pointer;
        filter:opacity(1) brightness(1);
    }
}
