.produceview{
    position:relative;
    width:25em;
    height:35em;
    border-radius:2em;
    background-color:rgba(0,0,0,0.2);
    box-shadow:0 0 15px rgba(0,0,0,0.35);
    overflow:hidden;
}
.background{
    width:100%;
    height:100%;
    background-repeat:no-repeat;
    background-position:center;
    background-size:cover;
    filter:blur(5em);
}
.foreground{
    position:absolute;
    width:100%;
    height:100%;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    left:0;
    top:0;
}
.image{
    width:50%;
    margin-bottom:3em;
    filter:drop-shadow(0 0 20px black);
}
.price{
    font-family:var(--majorFont);
    font-size:5em;
}
.discount{
    position:absolute;
    font-family:var(--majorFont);
    font-size:3em;
    right:0.25em;top:0.75em;
    color:var(--mainColor);
    transform:rotateZ(45deg);
}
