.todoitem{
    max-width:50em;
    display:flex;
    justify-content:flex-start;
    align-items:center;
}
.todoitem>input{
    margin-right:2em;
}
.description{
    flex:1;
    font-size:3em;
    text-decoration-thickness:0.1em;
    text-decoration-color:var(--textColor);
}

@media(min-width:15cm){
    .todoitem>input{
        transform:scale(1.5);
    }
}
