.configdetails{
    font-size:0.9em;
    list-style:inside;
    margin-left:10em !important;
}
.configdetails>*{
    margin-bottom:2em !important;
}
.configdetails>*>*:first-child{
    color:var(--mainColor);
}
.configdetails ul{
    font-size:0.85em;
    list-style:inside square;
}
.configdetails ul>li{
    font-size:5em;
    margin-top:0.25em;
}
.configdetails ul>li:first-of-type{
    margin-top:0;
}
.configdetails ul>li::marker{
    font-weight:700;
    color:var(--textColor);
}
.configdetails ul>li>*:first-child{
    display:inline;
}
.envbadge{
    font-size:0.65em;
    margin-left:1em !important;
}
.modes{
    list-style:inside;
}
.modes>li{
    font-size:5em;
    text-transform:capitalize;
}
.aliases>li{
    display:flex;
    justify-content:flex-start;
    align-items:center;
}
.aliases img{
    width:2em;
    margin:0 0.25em;
}
.definitions>li>*{
    color:var(--minorColor);
}
.servers>li{
    margin-bottom:1em !important;
}
.servers>li>*{
    margin-top:2em !important;
}
.servers>li>span{
    color:var(--minorColor);
}
