
@font-face {
    font-family:"notosans";
    src:url("./Assets/Fonts/NotoSans-Regular.ttf");
}

@font-face {
    font-family:"secularone";
    src:url("./Assets/Fonts/SecularOne-Regular.ttf");
}

@font-face {
    font-family:"helveticaneue";
    src:url("./Assets/Fonts/HelveticaNeueMedium.ttf");
}
*{
    margin:0;
    padding:0;
    outline:none transparent !important;
    -moz-user-select:none;
    -webkit-user-select:none;
    -ms-user-select:none;
    user-select:none;
}
:root{
    font-size:1vw;
    --mainFont:notosans;
    --majorFont:secularone;
    --minorFont:helveticaneue;
    --mainColor:#e03065;
    --majorColor:#ffffff;
    --minorColor:#73f5fa;
    --textColor:white;
    --backgroundColor:#382c5a;
}

html,body{
    height:100%;
    color:var(--textColor);
    overflow:hidden;
}
button,.button{
    font-size:1em;
    border:none;
    transition:200ms;
}


@media(max-width:15cm){
    :root{
        font-size:3vw;
    }
}

@media(hover:none){
    button:active,.button:active{
        filter:brightness(0.65);
        opacity:0.85;
    }
}

@media(any-hover){
    button:hover,.button:hover{
        cursor:pointer;
        filter:brightness(0.65);
        opacity:0.85;
    }
}

@supports(-webkit-touch-callout: none){
    *{
        overscroll-behavior:none;
    }
}
