.typetext{
    font-family:var(--mainFont);
    font-weight:bold;
    font-size:3em;
    letter-spacing:0.1em;
    white-space:nowrap;
    color:var(--textColor);
    /* margin-left:0.5em !important; */
    overflow-x:auto;
}
.typetext::-webkit-scrollbar{
    display:none;
}
.typetext[hashref]{
    transition:200ms;
}

@media(any-hover){
    .typetext[hashref]:hover{
        cursor:pointer;
        color:var(--mainColor) !important;
    }
}
