.scriptsblog{
    max-width:100%;
}
.scripts{
    list-style:inside square;
}
.scripts>li{
    margin-bottom:10em;
}
.scripts>li>*{
    margin-top:2em;
}
.scripts>li>*:first-child{
    margin-top:0;
}
.scripts>li::marker{
    font-size:5em;
}
.cmdname{
    letter-spacing:0.1em;
    color:var(--minorColor) !important;
}
.cmdname::first-letter{
    text-transform:lowercase !important;
}
.options{
    margin-left:5em !important;
}
