.featureexplorer{
    max-width:100%;
    --spacing:5em;
}
.header{
    max-width:100%;
    display:flex;
    justify-content:flex-start;
    align-items:center;
    white-space:nowrap;
    column-gap:2em;
    overflow-x:auto;
}
.header>*{
    flex-shrink:0;
    vertical-align:middle;
}
.backbtn{
    position:fixed;
    width:6em;
    left:auto;
    transform:translateX(-150%);
}
.title::first-letter{
    text-transform:none;
}
.description{
    margin-top:1em !important;
}
.experimentalicon{
    width:3em;
    vertical-align:middle;
}
.typevar{
    font-size:4em !important;
    color:var(--mainColor) !important;
}
.typevar span[id="sign"]{
    color:var(--mainColor);
}
.type[hashref]{
    color:var(--majorColor) !important;
    transition:200ms;
}


@media(any-hover){
    .type[hashref]:hover{
        cursor:pointer;
        color:var(--mainColor) !important;
    }
}

@media(max-width:15cm){
    .backbtn{
        position:relative;
        transform:translateX(0);
    }
}
