.hierarchyview{
    max-width:100%;
    padding:calc(var(--paddingHorizontal) / 2 - 1em) var(--paddingHorizontal) !important;
    border-radius:1em;
    background:linear-gradient(to right,rgba(0,0,0,0.08) 0%,transparent 35%);
    --paddingHorizontal:8em;
}
.hierarchyview>*{
    margin-top:1em !important;
}
.hierarchyview:first-of-type>*:first-child{
    margin-top:0 !important;
}
.header{
    position:relative;
    display:flex;
    justify-content:flex-start;
    align-items:center;
}
.icon{
    position:absolute;
    width:calc(var(--paddingHorizontal) / 2);
    top:0;bottom:0;margin:auto 0 !important;
    left:-1em;
    transform:translateX(-100%);
}
.name{
    font-size:3.5em;
}
.subhierarchy{
    padding:0 !important;
    margin-left:5em !important;
    background:transparent;
}
