.tableexample{
    width:100%;
}
.table{
    width:100%;
    font-size:5em;
    border-collapse:collapse;
    border:var(--border);
    --border:0.1em solid var(--mainColor);
    --padding:0.25em;
}
.table th{
    padding:var(--padding);
    text-transform:capitalize;
    color:var(--mainColor);
    border:var(--border);
}
.table td{
    padding:var(--padding);
    text-align:center;
    text-transform:capitalize;
    border:var(--border);
}
