.preview{
    max-width:100%;
    height:100em;
    align-self:center;
}
.header{
    width:100%;
    background-color:var(--mainColor);
}
.header>img{
    filter:invert(1);
}
.header>h3{
    font-size:5em;
    color:var(--textColor);
}
.routeview{
    height:calc(100% - var(--borderWidth));
    display:flex;
    justify-content:center;
    align-items:center;
    border:var(--borderWidth) solid var(--mainColor);
    border-top:none;
    background-color:rgba(0,0,0,0.15);
    --borderWidth:0.75em;
}
.routeview p{
    font-size:5em;
    text-align:center;
    color:var(--textColor);
}