.sendbutton{
    width:25em;
    display:flex;
    justify-content:space-around;
    align-items:center;
    padding:2em 3em;
    border-radius:1em;
    background-color:var(--mainColor);
}
.sendbutton>img{
    width:4em;
}
.sendbutton>label{
    font-family:var(--majorFont);
    font-size:3.5em;
    text-transform:uppercase;
    color:var(--textColor);
    cursor:inherit;
}
