.homescreen{
    position:relative;
    width:100%;
    height:100%;
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    align-items:stretch;
    background:radial-gradient(circle at 50%,var(--mainColor) -150%,transparent 100%);
    overflow:hidden auto;
}
.homescreen>*{
    z-index:1;
}
.popup{
    margin:auto;
    top:50%;
    left:25%;
}
.header,.footer{
    padding:1em;
}
.content{
    max-width:100%;
    flex:1;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    padding:0 2em;
    opacity:0;
    z-index:0 !important;
}
.content>*{
    filter:drop-shadow(0 0 1em rgba(0,0,0,0.1));
    z-index:1;
}
.description{
    font-family:var(--mainFont);
    font-size:1.6em;
    line-height:1.5em;
    letter-spacing:0.05em;
    text-align:center;
    text-transform:capitalize;
    max-width:20em;
    margin-bottom:1em;
}
.startbtn{
    font-size:1.25em;
    text-transform:capitalize;
    color:var(--majorColor);
    padding:0.75em 2em;
    border-radius:0.25em;
    opacity:0.65;
    background-color:var(--mainColor);
}

@media(any-hover){
    .startbtn:hover{
        opacity:1;
        filter:brightness(1);
        transform:scale(1.1);
    }
}

@media(hover:none){
    .startbtn:active{
        opacity:1;
        filter:brightness(1);
    }
}

@media(max-width:15cm){
    .name{
        font-size:4.5em;
    }
}
