.itemview{
    max-width:100%;
    padding:var(--padding);
    margin-top:2em;
    border-radius:1em;
    background-color:var(--majorColor);
    opacity:0.85;
    filter:drop-shadow(0 0 5px rgba(0,0,0,0.1));
    --padding:2em;
}
.itemview:first-of-type{
    margin-top:0;
}
.name{
    font-size:4em;
    text-align:left;
    text-transform:capitalize;
    color:var(--backgroundColor);
}
