.tictactoegame{
    width:fit-content;
    display:flex;
    justify-content:space-between;
    align-items:flex-start;
    font-size:0.5em;
}
.gameinfo{
    margin-left:15em;
}
.gameinfo>li{
    font-size:5em;
    margin-bottom:0.5em;
}
.gameinfo button{
    padding:0.5em 0.75em;
    border-radius:0.25em;
    color:var(--textColor);
    /* border:0.1em solid var(--minorColor); */
    background-color:rgba(0,0,0,0.1) !important;
}
