.searchbtn{
    position:relative;
    width:fit-content;
    display:flex;
    justify-content:flex-start;
    align-items:center;
}
.icon{
    width:2.5em;
}
.field{
    position:absolute;
    width:20em;
    height:2.5em;
    display:none;
    padding:0.5em 1em;
    right:150%;
    /* margin-right:1em; */
    border-radius:0.5em;
    background-color:rgba(0,0,0,0.1);
    color:var(--textColor);
    z-index:100;
}
.field>input{
    width:100%;
    height:100%;
    font-size:1.25em;
    color:inherit;
    border:none;
    background:none;
}
.field>input::placeholder{
    color:inherit;
    opacity:0.5;
    text-transform:capitalize;
}
.options{
    position:absolute;
    width:100%;
    height:10em;
    left:0;right:0;margin:0 auto;
    bottom:0;
    transform:translateY(100%);
    color:inherit;
    border-radius:0.5em;
    border-top-left-radius:0;
    border-top-right-radius:0;
    background-color:inherit;
    list-style:inside none;
    overflow:hidden auto;
}
.options::-webkit-scrollbar{
    width:0.2em;
    background-color:transparent;
}
.options::-webkit-scrollbar-thumb{
    border-radius:0.25em;
    background-color:var(--minorColor);
}
.options>li{
    max-width:100%;
    height:15%;
    display:flex;
    justify-content:flex-start;
    align-items:center;
    padding:0.5em 1em;
}
.options>li>span{
    font-size:0.75em;
    margin-left:0.5em;
    color:var(--mainColor);
}

@media(any-hover){
    .options>li:hover{
        cursor:pointer;
        background-color:rgba(255,255,255,0.05);
    }
}

@media(hover:none){
    .options>li:active{
        background-color:rgba(255,255,255,0.05);
    }
}

