.sectionview{
    max-width:100%;
}
.title{
    font-size:5em;
    text-transform:capitalize;
    color:var(--mainColor);
    margin-bottom:calc(var(--spacing) / 10) !important;
}
.fields{
    display:block;
    margin-left:var(--spacing) !important;
}
.fields>*:first-child{
    padding-top:0 !important;
}
.container{
    max-width:100%;
    display:flex;
    flex-direction:column-reverse;
    justify-content:flex-start;
    align-items:stretch;
    column-gap:5em;
}


@media(any-hover){
    .image:hover{
        cursor:zoom-in;
        transform:scale(1.05);
    }
}
