.tabNavigatorpreview{
    max-width:100%;
}
.tabnavigator{
    height:50em;
    background-color:rgba(0,0,0,0.05);
}
.header{
    background-color:transparent;
}
.header::-webkit-scrollbar{
    height:1em;
    background-color:transparent;
}
.header::-webkit-scrollbar-thumb{
    background-color:rgba(255,255,255,0.35);
}
.tabview{
    width:100%;
    height:100%;
    display:flex;
    justify-content:center;
    align-items:center;
    font-size:5em;
    text-transform:capitalize;
}
