.fieldview{
    position:relative;
    --borderTop:0.15em;
}
.fieldview:first-of-type{
    &>.bordertop,&>.header>.bordertop{
        display:none;
    }
}
.header{
    height:calc(3 * var(--spacing)) !important;
}
.bordertop{
    position:absolute;
    height:var(--borderTop);
    top:0;
    left:0;right:0;margin:0 auto !important;
    opacity:0.35;
    background-color:var(--majorColor);
}
