.pluginpreview{
    max-width:100%;
    display:flex;
    justify-content:space-around;
    align-items:center;
    column-gap:2em;
}
.pluginpreview img{
    width:95%;
    max-width:100em;
    align-items:center;
    order:1;
}
.description{
    font-family:var(--mainFont);
    font-size:3.733em;
}
.section{
    display:flex;
    flex-direction:column;
    justify-content:flex-start;
    align-items:center;
}
.title{
    font-family:var(--mainFont);
    font-size:3.733em;
    text-transform:capitalize;
    color:var(--majorColor);
    margin-bottom:0.5em;
}
.section>img{
    height:100em;
}

@media(max-width:15cm){
    .pluginpreview{
        flex-direction:column;
        justify-content:flex-start;
        align-items:center;
        row-gap:5em;
    }
}
