.getstartedscreen{
    max-width:100%;
    height:100%;
    display:flex;
    flex-direction:column;
    justify-content:flex-start;
    align-items:stretch;
    overflow:hidden auto;
}
.header{
    font-size:0.75em;
    box-shadow:0 0 10em rgba(0,0,0,0.15);
}
.main{
    flex:1;
    overflow:hidden;
}
.sidebar{
    border-top:0.5em solid var(--mainColor);
    background:linear-gradient(to top right,var(--mainColor) -1000%,transparent 50%) !important;
}

@media(max-width:15cm){
    .sidebar{
        border-right:none !important;
    }
}
