.addscreenblog{
    max-width:100%;
}
.steps{
    font-size:0.85em;
    list-style:inside decimal;
}
.steps>li{
    margin-bottom:5em;
}
.steps>li::marker{
    font-size:6.5em;
    color:var(--mainColor);
}
.steps>li>*:first-child::after{
    content:" :";
    color:var(--mainColor);
}
.addscreenblog .fileview{
    margin-top:5em;
}
