.produceaisle{
    grid-template-columns:repeat(3,1fr) !important;
}
.produceaisle>*{
    justify-self:center;
}
.name{
    position:sticky;
    font-size:5em;
    text-align:center;
    text-transform:capitalize;
    color:var(--mainColor);
    border:0.15em solid var(--mainColor);
    padding:0.5em 0;
    margin-top:0.5em;
    grid-column:span 3;
    justify-self:stretch;
}
