.preview{
    max-width:100%;
}
.message{
    font-size:3em;
    color:var(--textColor);
    margin-bottom:1em;
}
.sections{
    display:flex;
    justify-content:space-between;
    align-items:stretch;
    overflow:hidden;
    column-gap:3em;
}
.section{
    flex:1;
    overflow:hidden;
}
.title{
    max-width:100%;
    font-size:3em;
    text-align:center;
    color:var(--majorColor);
    margin-bottom:0.5em;
}
.title::first-letter{
    text-transform:uppercase;
}
.flatlist{
    width:100%;
    height:50em;
}
.horizontallist{
    height:auto;
}
.container{
    padding:1em var(--paddingHorizontal);
    --paddingHorizontal:2em;
}
.container::-webkit-scrollbar{
    width:2em;
}
.container::-webkit-scrollbar-thumb{
    border-radius:2em;
    background-color:rgba(255,255,255,0.1);
}
.container::-webkit-scrollbar-track{
    border-radius:2em;
    background-color:rgba(255,255,255,0.1);
}
.horizontalitem{
    width:calc(100% - 2 * var(--paddingHorizontal));
    display:inline-block;
    margin-left:calc(2 * var(--paddingHorizontal));
}
.horizontalitem:first-of-type{
    margin-left:0;
}

@media(max-width:15cm){
    .sections{
        flex-wrap:wrap;
    }
    .section{
        min-width:100%;
        margin-left:0;
        margin-top:4em;
    }
    .section:first-of-type{
        margin-top:0;
    }
}
