.bubblingview{
    position:absolute;
    width:100%;
    height:100%;
    inset:0;margin:auto;
    overflow:hidden;
    z-index:0 !important;
}
.bubble{
    position:absolute;
    width:0.5em;
    height:0.5em;
    left:0;
    top:0;
    opacity:0.25;
    border-radius:50%;
    will-change:transform;
    filter:drop-shadow(0 0 0 3px rgba(0,0,0,0.1));
    transition-timing-function:ease-out;
}
.bubble:nth-of-type(2n+1){
    background:var(--minorColor);
}
.bubble:nth-of-type(2n){
    background:var(--mainColor);
}
