.fieldbody{
    max-width:100%;
    padding-bottom:var(--spacing) !important;
}
.description{
    line-height:5em;
}
.component{
    max-width:100%;
    display:block;
    margin-top:3.5em !important;
}
.subfields{
    display:block;
    margin-left:5em;
}
.subfields:first-child{
    margin-top:calc(-1 * var(--spacing)) !important;
}
.noticeview{
    margin-top:4em !important;
}
