.textview{
    font-size:1em;
    color:var(--textColor);
}
.textview>p{
    font-size:3.75em;
    line-height:1.5em;
    color:inherit;
    margin-top:0.5em !important;
}
.textview>p:first-of-type{
    margin-top:0 !important;
}
.textview>p::first-letter{
    text-transform:uppercase;
}
.textview>p[sentence]::after{
    content:".";
    display:inline;
}
.textview a,.textview span,.textview b,.textview strong{
    color:var(--minorColor);
}
